<template>
    <div id="back-top">
        <transition name="backTop">
            <div :style="{'right': styleRight,'bottom': styleBottom}"
                 @click.stop="handleClick"
                 class="back-top pointer"
                 v-if="visible">
                <!--<slot>-->
                <i class="iconfont iconTop"></i>
                <!--</slot>-->
            </div>
        </transition>
    </div>
</template>

<script>
  export default {
    name: 'back-top',
    props: {
      target: [String],
      scroll: [Number],
      visibilityHeight: {
        type: Number,
        default: 100
      },
      right: {
        type: Number,
        default: 40
      },
      bottom: {
        type: Number,
        default: 40
      }
    },
    data() {
      return {
        type: null,
        el: null,
        container: null,
        visible: false
      }
    },
    computed: {
      styleBottom() {
        return `${this.bottom}px`
      },
      styleRight() {
        return `${this.right}px`
      }
    },
    mounted() {
      this.container = document
      this.el = document.documentElement
      if (this.target) { // target方式
        this.el = document.querySelector(this.target)
        if (!this.el) {
          throw new Error('未找到target: ' + this.target)
        }
        this.container = this.el
        this.type = 'target'
        this.container.addEventListener('scroll', this.onScroll)
      } else { // 自定义方式
        this.type = 'custom'
        this.$watch('scroll', function (newVal, oldVal) {
          // 做点什么
          this.visible = newVal > 0
          this.$emit('changeCallback', newVal, oldVal)
        })
      }
    },
    methods: {
      /**
       * 滚动条控制是否显示
       */
      onScroll() {
        let scrollTop = this.el.scrollTop
        this.visible = scrollTop >= this.visibilityHeight
      },
      handleClick(e) {
        if (this.type === 'target') {
          this.scrollToTop()
        } else if (this.type === 'custom') {
          // this.scroll = 0
        }
        this.$emit('click', e)
      },
      scrollToTop() {
        let el = this.el
        let step = 0
        let interval = setInterval(function () {
          if (el.scrollTop <= 0) {
            clearInterval(interval)
            return
          }
          step += 10
          el.scrollTop -= step
        }, 20)
      }
      /**
       * 回到顶部
       */
    }
  }
</script>

<style lang="less">
    .back-top {
        position: fixed;
        background: black;
        width: 48px;
        height: 48px;
        border-radius: 4px;
        text-align: center;
        bottom: 2rem;
        right: 2rem;
        z-index: 999;

        i {
            font-size: 24px;
            line-height: 48px;
            color: white;
        }
    }

    // gotop按钮渐变
    .backTop-enter-active, .backTop-leave-active {
        transition: opacity .5s ease;
    }

    .backTop-enter, .goTop-leave-to /* .fade-leave-active below version 2.1.8 */ {
        opacity: 0;
    }

</style>
