<template>
  <div class="news" id="news">
    <img alt="" src="@/assets/images/news/banner.png">
    <!-- 新闻主体 -->
    <div class="title">
      <div :class="{active:current1}" @click="choose1" class="tab pointer">
        <p>公司新闻</p>
        <p>Company News</p>
      </div>
      <div :class="{active:current2}" @click="choose2" class="tab pointer">
        <p>行业新闻</p>
        <p>Industry News</p>
      </div>
    </div>
    <news-company @heightChange="heightChange" v-show="current1"></news-company>
    <news-industry @heightChange="heightChange" v-show="current2"></news-industry>
    <!--    todo 先做个标记，safar浏览器，新闻图片被拉高-->
    <back-top target=".news"></back-top>
    <app-footer></app-footer>
  </div>
</template>

<script>
  import appFooter from '@/components/app-footer'
  import newsCompany from '@/pages/news/news-company'
  import newsIndustry from '@/pages/news/news-industry'
  import backTop from '@/components/back-top'

  export default {
    name: 'news',
    components: {
      appFooter,
      newsCompany,
      newsIndustry,
      backTop
    },
    data() {
      return {
        current1: true,
        current2: false
      }
    },
    mounted() {
      this.$store.commit('setBackground', true)
    },
    methods: {
      heightChange() {
        setTimeout(() => {
          this.$refs.customScrollRef.init()
        }, 300)
      },
      choose1() {
        this.current1 = true
        this.current2 = false
      },
      choose2() {
        this.current1 = false
        this.current2 = true
      }
    }
  }
</script>

<style lang="less" scoped>
  .news {
    height: 100%;
    overflow: auto;
    text-align: center;

    img {
      width: 100%;
      margin-top: 80px;
    }

    .title {
      display: flex;
      flex-flow: row nowrap;
      justify-content: center;

      .tab {
        margin: 5rem 7rem 3rem;

        p {
          &:first-child {
            font-size: 1.6rem;
            font-weight: 600;
            color: rgba(160, 160, 160, 1);
          }

          &:last-child {
            font-size: 1.2rem;
            color: rgba(200, 200, 200, 1);
          }
        }
      }

      .active {
        p {
          &:first-child {
            color: rgba(20, 32, 94, 1);
          }

          &:last-child {
            color: rgba(20, 32, 94, 1);
          }
        }
      }
    }
  }
</style>
